<template>
    <div style="min-width: 650px">
        <top-nav title="商品规格"></top-nav>
        <div class="wyu-tips wyu-tips-warning">
            <p> 商品规格是购买商品时给用户选择的, 涉及到价格变动库存等, 例如:衣服的 颜色 尺寸 等</p>
        </div>
        <div class="wyu-main">
            <div class="wyu-block" v-loading="loading">
                <el-form  size="mini" v-model="searchForm" class="search-form" :inline="true">
                    <el-form-item v-loading="typeLoading" label="所属模型" label-width="100">
                        <el-select v-model="searchForm.type_id" placeholder="请选择">
                            <el-option
                                v-for="item in types"
                                :key="item.type_id"
                                :label="item.name"
                                :value="item.type_id">
                        </el-option>
                        </el-select>
                    </el-form-item>
                    <div style="padding-left:80px;">
                        <el-button icon="el-icon-search" @click="getList(1)" size="mini" type="primary">搜索</el-button>
                        <!--<a href="javascript:void(0);" @click="searchForm.title = '',getList(1)">取消搜索</a>-->
                        <el-button size="mini" @click="creatEvent" type="danger">新增商品规格</el-button>
                    </div>
                </el-form>
                <el-table :data="datas.list" style="width: 100%" >
                    <el-table-column    prop="type_id" label="ID"  width="80"></el-table-column>
                    <el-table-column    prop="name" label="规格名称"  width="100"></el-table-column>
                    <el-table-column    prop="name" label="所属模型" width="200" >
                        <template slot-scope="scope">
                            <div class="ellipsis-01">
                                {{idByTypeName(scope.row.type_id)}}
                            </div>
                        </template>
                    </el-table-column>
                    <el-table-column    prop="name" label="规格项" >
                        <template slot-scope="scope">
                            <div class="ellipsis-01">
                                <span v-for="(val,key) in scope.row.item">{{key  == 0 ? '' :'，'}}{{val.item}}</span>
                            </div>
                        </template>
                    </el-table-column>
                    <el-table-column   prop="orderby" label="排序"  width="200"></el-table-column>
                    <el-table-column label="操作"  >
                        <template slot-scope="scope">
                            <el-button size="mini" @click="editDataEvent(scope.row)" type="text">编辑</el-button>
                            <el-popover style="margin: 0 20px;" placement="top" width="160" v-model="scope.row.is_delete">
                                <p style="margin-bottom: 4px">确定删除吗？</p>
                                <div style="text-align: right; margin: 0">
                                    <el-button  :loading="loading" size="mini" type="text" @click="scope.row.is_delete = false">取消
                                    </el-button>
                                    <el-button :loading="loading" type="primary" size="mini" @click="del(scope.row)">确定
                                    </el-button>
                                </div>
                                <el-button type="text" size="mini"  slot="reference">删除</el-button>
                            </el-popover>
                        </template>
                    </el-table-column>
                </el-table>
                <br/>
                <div class="page">
                    <div class="page-mian">
                        <el-pagination @current-change="getList" :page-size="datas.limit"
                                       layout="total, prev, pager, next" :total="datas.count">
                        </el-pagination>
                    </div>
                </div>
            </div>
        </div>

        <!--编辑新增 弹窗-->
        <el-dialog title="商品模型" v-loading="subLoading" :visible.sync="dialog" width="500px" :modal-append-to-body="false">
            <el-form :model="form" style="padding: 0" size="small" label-width="80px">
                <el-form-item label="规格名称" >
                    <el-input v-model="form.name" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item v-loading="typeLoading" label="所属模型" >
                    <el-select v-model="form.type_id" placeholder="请选择">
                        <el-option
                                v-for="item in types"
                                :key="item.type_id"
                                :label="item.name"
                                :value="item.type_id">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="规格项" style="max-height: 250px;overflow-y: auto">
                    <div v-for="(_,key) in form.item" :key="key" >
                        <el-input  v-model="form.item[key].item" >
                            <el-button   @click="form.item.splice(key,1)" slot="append" icon="el-icon-delete"></el-button>
                        </el-input>
                    </div>
                    <el-button  icon="el-icon-plus" @click="form.item.push({item:''})">添加规格项</el-button>
                    <p class="wyu-tips"> 如：黑色 、白色</p>
                </el-form-item>
                <el-form-item label="排序" >
                    <el-input v-model="form.orderby" autocomplete="off"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button size="small" @click="dialog = false">取 消</el-button>
                <el-button size="small" type="primary" @click="creatData(true)">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    import TopNav from "@/components/TopNav";
    import * as api from "@/api";

    export default {
        name: 'goodsSpecIndex',
        components: {TopNav},
        data() {
            return {
                //获取模型列表菊花
                typeLoading:true,
                //修改 添加 弹窗  菊花
                subLoading: false,
                //整体列表 菊花
                loading: false,
                //列表数据 包含 list count limit
                datas: [],
                //数据
                form:this.initForm(),
                //修改 添加 状态控制
                dialog: false,
                isUpdate:false,
                //搜索 form
                searchForm: {
                    type_id: 0,  // 物流公司标题
                },
                //types
                types:[],
            }
        },

        created() {
            if(this.$route.params.typeid){
                this.searchForm.type_id =  +this.$route.params.typeid ;
            }
            this.getTypeList();
            this.getList();
        },
        methods: {

            idByTypeName(type_id){
                let name  = "";
                this.types.some((item,key)=>{
                    if(item.type_id == type_id){
                        name = item.name;
                        return true;
                    }
                });
                return name;
            },

            //初始化form数据
            initForm(){
                return {
                    name:'', //名称
                    type_id:0,
                    orderby:0,
                    item:[{item:''}],
                }
            },
            //新增物流模板事件
            creatEvent(){
                this.isUpdate = false;
                this.form =  this.initForm();
                this.form.type_id = this.searchForm.type_id;
                this.dialog = true;
            },

            //修改物流模板
            editDataEvent(row){
                this.isUpdate = true;
                this.form =  row;
                this.dialog = true;
            },

            //新增数据
            creatData(){
                this.subLoading = true;
                let act = this.isUpdate ? 'edit' : 'create';
                api.post("/shop/admin.spec/" +act ,{
                    appToken: this.$store.getters.appToken,
                    name:this.form.name,
                    type_id:this.form.type_id,
                    spec_id:this.form.spec_id,
                    orderby:this.form.orderby,
                    item:JSON.stringify(this.form.item),
                }).then(res => {
                    this.subLoading = false;
                    this.form = this.initForm();
                    this.$message({
                        message: '恭喜你，操作成功',
                        type: 'success'
                    });
                    this.dialog = false;
                    this.getList();
                }).catch((error) => {
                    this.subLoading = false;
                });
            },

            getTypeList(){
                this.typeLoading =true;
                api.post("/shop/admin.goodstype/index", {
                    appToken: this.$store.getters.appToken,
                    name:this.searchForm.title,
                }).then(res => {
                    this.typeLoading =false;
                    this.types = res.list;
                    this.types.unshift({
                        type_id:0,
                        name:"全部模型"
                    });

                }).catch((error) => {
                    this.typeLoading =false;
                });
            },


            //获取物流公司
            getList(page = 1){
                this.loading =true;
                api.post("/shop/admin.spec/index", {
                    appToken: this.$store.getters.appToken,
                    type_id:this.searchForm.type_id,
                }).then(res => {
                    this.loading =false;
                    this.datas = res;
                }).catch((error) => {
                    this.loading =false;
                });
            },
            //删除快递公司
            del(row) {
                this.loading = true;
                api.post("/shop/admin.spec/del", {
                    appToken: this.$store.getters.appToken,
                    spec_id:row.spec_id,
                }).then(res => {
                    this.loading = false;
                    this.$message({
                        message: '恭喜你，操作成功',
                        type: 'success',
                    });
                    this.getList();
                }).catch((error) => {
                    this.loading = false;
                });
            },

        }
    }
</script>

